.signin{
  	width: 50%;
	display: flex;
	flex-direction: column;
	backdrop-filter: blur(4px) saturate(89%);
	-webkit-backdrop-filter: blur(4px) saturate(89%);
	background-color: rgba(49, 73, 73, 0);
	border-radius: 12px;
	border: 1px solid rgba(209, 213, 219, 0.3);
	-webkit-box-shadow: 0px 4px 123px -40px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 0px 4px 123px -40px rgba(0, 0, 0, 0.75);
	box-shadow: 0px 4px 123px -40px rgba(0, 0, 0, 0.75);

	padding: 20px;

  @media screen and(max-width: 800px) {
    width: unset;
  }

  .title {
    margin: 10px 0;
  }

  .buttons {
    display: flex;
    justify-content: space-between;
  }
  img {
		height: 40px;
		padding-top: 5px;
	}
}